/* App.css */

/* Modal Background */
.fixed {
  position: fixed;
}
.inset-0 {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.bg-black {
  background-color: #000000;
}
.z-50 {
  z-index: 50;
}
.flex {
  display: flex;
}
.items-center {
  align-items: center;
}
.justify-center {
  justify-content: center;
}

/* Modal Container */
.bg-white {
  background-color: #ffffff;
}
.p-6 {
  padding: 1.5rem;
}
.rounded-lg {
  border-radius: 0.5rem;
}
.max-w-4xl {
  max-width: 64rem;
}
.w-full {
  width: 100%;
}
.mx-4 {
  margin-left: 1rem;
  margin-right: 1rem;
}

/* Header Styles */
.text-xl {
  font-size: 1.25rem;
}
.font-bold {
  font-weight: 700;
}
.mb-4 {
  margin-bottom: 1rem;
}

/* Close Button */
.text-gray-500 {
  color: #6b7280;
}
.hover\:text-gray-700:hover {
  color: #374151;
}

/* Image Styling */
.w-full {
  width: 100%;
}
.h-auto {
  height: auto;
}
.mb-4 {
  margin-bottom: 1rem;
}

/* Metadata Styling */
.space-y-2 > :not([hidden]) ~ :not([hidden]) {
  --tw-space-y-reverse: 0;
  margin-top: calc(0.5rem * calc(1 - var(--tw-space-y-reverse)));
  margin-bottom: calc(0.5rem * var(--tw-space-y-reverse));
}
.text-gray-700 {
  color: #4b5563;
}
.font-semibold {
  font-weight: 600;
}

/* Responsive Styling */
@media (max-width: 768px) {
  .p-6 {
    padding: 1rem;
  }
  .text-xl {
    font-size: 1rem;
  }
  .max-w-4xl {
    max-width: 90%;
  }
}

@media (max-width: 480px) {
  .p-6 {
    padding: 0.75rem;
  }
  .text-xl {
    font-size: 0.875rem;
  }
}